/**
 * Styles
 *
 * The following styles get applied to the WordPress Admin
 * area.
 */
.settings_page_wholesome_examples {
  background: #f6f6f6;
  box-sizing: border-box;
  font-family: Helvetica Neue, Helvetica, Arial, Lucida Grande, sans-serif;
  font-size: 14px;
  position: relative; }
  .settings_page_wholesome_examples #wpcontent {
    padding-left: 0; }
  .settings_page_wholesome_examples .auto-fold #wpcontent {
    padding-left: 0; }

#wholesome-examples-settings .components-placeholder {
  background: #f6f6f6; }

#wholesome-examples-settings .wholesome-examples-header {
  background-color: #ffffff;
  box-shadow: 0 1px 0 rgba(213, 213, 213, 0.5), 0 1px 2px #eeeeee;
  padding: 20px 10px; }
  #wholesome-examples-settings .wholesome-examples-header .wholesome-examples-container {
    margin: 0 auto;
    max-width: 750px; }
    #wholesome-examples-settings .wholesome-examples-header .wholesome-examples-container .wholesome-examples-logo {
